interface Stuff {
  name: string;
  nickname: string;
  password: string;
  profilePicture: string;
  role: string;
  sex: string;
  email: string;
  hobby: string;
  birth: string;
  age: number;
}

export const formValidator = (stuff: Stuff) => {
  let error: string;
  error = "";
  const EMAIL_RULE =
    /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
  const BIRTH_RULE = /^[1-2]{1}[0-9]{3}-[0-1]{1}[0-9]{1}-[0-3]{1}[0-9]{1}$/g;

  if (stuff.name.length < 2) {
    error += " 2  . \n";
  }
  if (stuff.nickname.length < 2) {
    error += " 2  . \n";
  }
  if (stuff.password.length < 4) {
    error += " 4  . \n";
  }
  if (stuff.profilePicture === "/.jpg") {
    error += "   . \n";
  }
  if (!stuff.role.length) {
    error += " . \n";
  }
  if (!stuff.sex) {
    error += " . \n";
  }
  if (!EMAIL_RULE.test(stuff.email)) {
    error += "   . \n";
  }
  if (!stuff.hobby) {
    error += " . \n";
  }
  if (!BIRTH_RULE.test(stuff.birth)) {
    error += "   . \n";
  }
  if (stuff.age <= 0 || stuff.age > 110) {
    error += "    . \n";
  }
  if (error !== "") {
    error =
      "  .   . \n\n" + error;
    alert(error);
    return false;
  }
  return true;
};
